import { collection, addDoc, getDocs, deleteDoc, doc, updateDoc, query, where } from "firebase/firestore";
import { auth, db } from "./firebase.js";

// Adds a new habit for the authenticated user
export async function addHabit(name) {
  const user = auth.currentUser;
  if (!user) {
    console.error("User is not authenticated");
    return false;
  }
  try {
    await addDoc(collection(db, "habits"), {
      name,          // Habit name
      streak: 0,     // Initial streak is 0
      lastCheckIn: null,
      email: user.email.toLowerCase()
    });
    await loadHabits();
    return true;
  } catch (error) {
    console.error("Error adding habit:", error);
    return false;
  }
}

// Loads habits for the current user and displays them in the UI
export async function loadHabits() {
  const habitList = document.getElementById("habitList");
  if (!habitList) return;
  
  const user = auth.currentUser;
  if (!user) {
    console.error("User is not signed in");
    habitList.innerHTML = "<li>Please sign in to view your habits.</li>";
    return;
  }
  
  try {
    const habitsCollection = collection(db, "habits");
    const q = query(habitsCollection, where("email", "==", user.email.toLowerCase()));
    const snapshot = await getDocs(q);
    const habits = snapshot.docs.map(docSnap => ({ id: docSnap.id, ...docSnap.data() }));
    
    habitList.innerHTML = "";
    if (habits.length === 0) {
      habitList.innerHTML = "<li>No habits found. Add a new habit.</li>";
    } else {
      habits.forEach(habit => displayHabit(habit, habitList));
    }
  } catch (error) {
    console.error("Error loading habits:", error);
    habitList.innerHTML = "<li>Error loading habits.</li>";
  }
}

// Helper to display a habit in the UI
export function displayHabit(habit, habitList) {
  const li = document.createElement("li");
  li.innerHTML = `
    <span>${habit.name}</span>
    <span>ðŸ”¥ Streak: ${habit.streak} days</span>
    <button class="edit-btn" data-id="${habit.id}">Edit</button>
    <button class="delete-btn" data-id="${habit.id}">Delete</button>
    <button class="checkin-btn" data-id="${habit.id}" data-streak="${habit.streak}" data-lastcheckin="${habit.lastCheckIn}">Check-in</button>
  `;
  habitList.appendChild(li);
}

// Edits a habit's name
export async function editHabit(habitId, newName) {
  try {
    await updateDoc(doc(db, "habits", habitId), { name: newName });
    await loadHabits();
    return true;
  } catch (error) {
    console.error("Error editing habit:", error);
    return false;
  }
}

// Deletes a habit
export async function deleteHabit(habitId) {
  try {
    await deleteDoc(doc(db, "habits", habitId));
    await loadHabits();
    return true;
  } catch (error) {
    console.error("Error deleting habit:", error);
    return false;
  }
}

// Updates a habit's check-in (i.e. streak) if not already checked in today
export async function checkInHabit(habitId, currentStreak, lastCheckIn) {
  const today = new Date().toDateString();
  if (lastCheckIn === today) {
    alert("You already checked in today!");
    return;
  }
  let newStreak;
  const yesterday = new Date();
  yesterday.setDate(yesterday.getDate() - 1);
  if (lastCheckIn === yesterday.toDateString()) {
    newStreak = currentStreak + 1;
  } else {
    newStreak = 1;
  }
  try {
    await updateDoc(doc(db, "habits", habitId), {
      streak: newStreak,
      lastCheckIn: today
    });
    await loadHabits();
    return true;
  } catch (error) {
    console.error("Error checking in habit:", error);
    return false;
  }
}
